<template>
  <div>
    <myBreadcrumb :breadList="breadList" />
    <div class="classicMore lightest-bg">
      <div class="book" v-for="(item, index) in bookList" :key="index">
        <div class="bookImg">
          <img
            :src="item.images.detail"
            :alt="item.title"
            @click="goNews(item)"
          />
        </div>
        <p class="classic-title">{{ item.title }}</p>
        <a href="javascript:void(0)" class="lightest-box">
          <div class="bookBt" @click="goNews(item)">立刻收听</div>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import myBreadcrumb from "../../../components/bread-crumb";
import { updatedBreadCrumb } from "@/assets/js/util.js";
export default {
  data() {
    return {};
  },
  created() {
    let title = sessionStorage.getItem("ClassicLabel");
    let data = {};
    data.path = this.$route.path;
    data.name = title;
    // data.details=_this.list.title
    updatedBreadCrumb(data, 1, this);
  },
  methods: {
    goNews(item, index) {
      localStorage.setItem("ClassicId", item.id);
      this.$router.push({
        path: "/classic/details" //跳转的路由
      });
    }
  },
  computed: {
    bookList() {
      return this.$store.state.bookList;
    },
    breadList() {
      return this.$store.state.breadList;
    }
  },
  components: {
    myBreadcrumb
  }
};
</script>
<style lang="scss">
.classicMore {
  overflow: hidden;
  width: 1200px;
  margin: 22px auto 0;

  .book {
    width: 189px;
    height: 300px;
    opacity: 1;
    margin-top: 10px;
    margin-left: 10px;
    float: left;
    position: relative;
    .bookImg {
      width: 180px;
      height: 180px;
      cursor: pointer;
    }
    img {
      width: 100%;
      height: 100%;
    }
    .classic-title {
      text-align: center;
      margin-top: 4px;
    }
    a {
      text-decoration: none;
      .bookBt {
        width: 45px;
        height: 45px;
        border: 1px solid;
        font-size: 12px;
        text-align: center;
        padding: 5px;
        border-radius: 50%;
        position: absolute;
        bottom: 10px;
        left: 70px;
      }
      .bookBt:hover {
        color: #fff;
        background: #584945;
      }
    }
  }
}
</style>
